.container{
    width: 690px;
    margin: 50px 0 0 100px;
    position: relative;
}
.wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-bottom: 150px;
}
@media(max-width:1100px){
    .container{
        width: 100%;
        margin: 0 auto;
    }
}